    export default {
        props: {
            video_id: {
                type: Number,
                default: 0
            },

            video_url: {
                type: String,
                default: ''
            },
            play: {
                type: Boolean,
                default: false
            },
            height: {
                type: String,
                default: ''
            },
            width: {
                type: String,
                default: ''
            }
        },
		data() {
        	return {
				loading: true,
                videoCtx: null,
                controls: false
			}
		},
        methods: {
            videoPlay() {

				this.videoCtx = uni.createVideoContext(`video_${this.video_id}`, this);




				if (this.play) {
                    this.videoCtx.play();
                } else {
                    this.videoCtx.pause();
                }
            },
			waiting() {
            	this.loading = false;
			},
			pause() {
			},
			setPlay() {
				this.loading = true;
			}
        },
        watch: {
            play: {
            	handler() {
			        this.videoPlay();
			    },
            },
        },
		beforeDestroy() {
			this.videoCtx = null;
		}
	};
